import React from 'react'
import { Link, useLocation } from 'react-router-dom'


function Navbar() {
  let location = useLocation();
  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
          <Link className="navbar-brand" to="/home">Elda Health</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className={`nav-link ${location.pathname === '/home' ? "active" : ""}`} aria-current="page" to="/">Home</Link>
              </li>
              <li className="nav-item">
                <Link className={`nav-link ${location.pathname === '/shop' ? "active" : ""}`} to="/shop">Shop</Link>
              </li>
              <li className="nav-item">
                <Link className={`nav-link ${location.pathname === '/media' ? "active" : ""}`} to="/media">Media</Link>
              </li>
              <li className="nav-item">
                <Link className={`nav-link ${location.pathname === '/join' ? "active" : ""}`} to="/join">Join Yoga</Link>
              </li>
            </ul>


            <Link to="/"><button className='btn btn-danger' >Logout</button></Link>
          </div>
        </div>
      </nav>
    </>
  )
}

export default Navbar;
